import React from 'react'
import { withRouter } from 'react-router-dom'
import {Elements} from '@stripe/react-stripe-js'
import {loadStripe} from '@stripe/stripe-js'
import StripeCheckoutForm from './StripeCheckoutForm'
import UserInfoView from './UserInfo/UserInfoView'
import { imagesCareerPro } from 'Shared/layouts/CareerPro/Components/imagesCareerPro'
import { OnlyBoxLoader } from 'Shared/helpers/skeletonLoader'

const stripePromise = loadStripe(process.env.REACT_APP_STRIPE_KEY)

function StripeElementView (props) {
  const { selectedPackageId, formStep, setFormStep, stripeUserForm, setStripeUserForm, handleApiStripeIntent, fromChangeHandler } = props

  const ICONS = {...imagesCareerPro}
  const options = {
    clientSecret: props.clientSecret
  }

  return (<React.Fragment>
    <div className={`content ${(formStep === 1) && 'active-content'}`}>
      <UserInfoView stripeUserForm={stripeUserForm} setStripeUserForm={setStripeUserForm} fromChangeHandler={fromChangeHandler} formStep={formStep} setFormStep={setFormStep} handleApiStripeIntent={handleApiStripeIntent} selectedPackageId={selectedPackageId} />
    </div>

    <div className={`content ${(formStep === 2) && 'active-content' }`}>
      { options.clientSecret
        ? <React.Fragment>
          <button className={`${(formStep === 1) && 'tabs-active'} tabs back_step_arrow`} onClick={() => setFormStep(1)}>
            <img src={ICONS.slider_left_arrow} alt='back icon' /> Back
          </button>
          <Elements stripe={stripePromise} options={options}>
            <StripeCheckoutForm package_id={selectedPackageId} stripeUserForm={stripeUserForm} />
          </Elements>
        </React.Fragment> : <div>
          <OnlyBoxLoader />
        </div>
      }
    </div>
  </React.Fragment>)
};

export default withRouter(StripeElementView)



