import React from 'react'
import withTranslation from 'Shared/hoc/withTranslations'
import './CareerProCheckoutBenefits.scss'

function CareerProCheckoutBenefits (props) {
  const { selectedPlan, t } = props
  const features = (selectedPlan && selectedPlan.features) && selectedPlan.features.split('|');

  if (!selectedPlan || !features) return <div>"loading..."</div> // WIP HOC

  return (
    <div className='order-review__benefits'>
      <div className='order-review__benefits-title'>{t('careerProPage.benefits.title')}:</div>

      { features && features.map((content, index) =>
        <div key={index} className='benefits_plan'>
          <div className='order-review__benefit'>
            <i className='order-review__benefit-icon' />
            <span className='order-review__benefit-description'> {content} </span>
          </div>
        </div>)
      }

    </div>
  )
}

export default withTranslation(CareerProCheckoutBenefits)



