import { connect } from 'react-redux'
import { getLocationPhoneNumber, isOlivReachable } from 'Shared/selectors/geolocation'
import { formatPhoneNumber } from '../helpers/formats'

const mapStateToProps = state => ({
  phoneNumber: formatPhoneNumber(getLocationPhoneNumber(state)),
  isOlivReachable: isOlivReachable(state)
})

export default connect(mapStateToProps)



