import React, { Component } from 'react'
import { openIntercomWithNewMessage } from 'Intercom/components/IntercomComponent'
import { PrimaryButton } from 'Shared/components/Button'
import payPerHireImage from 'Shared/assets/images/vectors/pay_per_hire.svg'
import cvSearchImage from 'Shared/assets/images/vectors/cv_search.svg'
import jobSlotsImage from 'Shared/assets/images/vectors/job_slots.svg'
import subscriptionPlansImage from 'Shared/assets/images/vectors/subscription_plans.svg'
import withTranslations from 'Shared/hoc/withTranslations'
import {
  getPriceWithCurrency,
  saveSelectedJobPosts,
  calculateJobsTotalPrice,
  calculateJobsTotalSavings
} from 'Shared/helpers/payment'
import { NumericInput } from 'Shared/components/FormElements'
import { selectJobPostLimits } from 'Shared/constants/Job'
import Tooltip from 'Shared/components/Tooltip/Tooltip'
import { isMobile } from 'Shared/helpers/mobile'
import { formatAfterFive, parseNumericInput } from 'Shared/helpers/formats'
import { segmentSpec } from 'TrackingScripts/scripts/employer/segment'

class JobsProducts extends Component {
  componentDidMount () {
    this.props.fetchCurrentLocationIfNeeded()
  }

  state = {
    jobPostsAmount: 1,
    tooltipEventType: isMobile() ? 'click' : 'mouseenter',
    creatingNewJobOffer: false
  }

  inputProps = () => ({
    input: {
      onChange: value => this.setState({ jobPostsAmount: value }),
      value: this.state.jobPostsAmount
    },
    format: formatAfterFive(this.state.jobPostsAmount),
    parse: parseNumericInput(5),
    precision: 0,
    step: 1,
    meta: {}
  })

  onSelectJobs = () => {
    saveSelectedJobPosts(this.state.jobPostsAmount)
    segmentSpec.selectedJobPosts(this.state.jobPostsAmount, this.props.userEmail, this.props.userId)
    return this.props.history.push('/pricing/job-posts/review')
  }

  handlePostJob = async () => {
    this.setState({ creatingNewJobOffer: true })
    await this.props.createNewEmptyJob()
    return this.setState({ creatingNewJobOffer: false })
  }

  handleFreeCta = () => {
    return this.props.isEmployer ? this.handlePostJob() : this.props.history.push('/employer/sign-up')
  }

  getJobProductsCard = () => {
    const { t, price, currency, isEmployer } = this.props
    const { jobPostsAmount } = this.state
    const isBetweenOneAndFive = jobPostsAmount >= 1 && jobPostsAmount <= 5
    const savings = calculateJobsTotalSavings(jobPostsAmount, price)

    switch (true) {
      case this.props.isFreePlansPage:
        return (
          <div className='job-products__card job-products__card--free'>
            <div className='job-products__card-heading'>
              <div>
                <div className='job-products__card-heading-title'>
                  {t('plansPage.products.jobPost.title')}
                </div>
                <div className='job-products__card-heading-description'>
                  {t('plansPage.products.jobPost.description')}
                </div>
              </div>
            </div>
            <div className='job-products__card-content'>
              <div className='job-products__card-price'>
                Free
              </div>
            </div>
            <div className='job-products__card-btn-container'>
              <PrimaryButton disabled={this.state.creatingNewJobOffer} onClick={this.handleFreeCta}>
                {isEmployer ? <span><i className='ion-md-add' />&nbsp;{t('common.postJob')}</span> : t('plansPage.products.jobPost.freeBtn')}
              </PrimaryButton>
            </div>
          </div>
        )
      default:
        return (
          <div className='job-products__card'>
            <div className='job-products__card-heading'>
              <div>
                <div className='job-products__card-heading-title'>
                  {t('plansPage.products.jobPost.title')}
                </div>
                <div className='job-products__card-heading-description'>
                  {t('plansPage.products.jobPost.description')}
                </div>
              </div>
              <div className='job-products__card-heading-price'>
                {isBetweenOneAndFive && getPriceWithCurrency(price, currency)}
              </div>
            </div>
            <div className='job-products__card-content'>
              <div className='job-products__card-price-label'>{t('plansPage.products.jobPost.total')}</div>
              <div className='job-products__card-price'>
                {
                  isBetweenOneAndFive
                    ? getPriceWithCurrency(calculateJobsTotalPrice(jobPostsAmount, price), currency)
                    : t('plansPage.products.jobPost.customPricing')
                }
                {
                  <span className='job-products__card-price-vat'>{ isBetweenOneAndFive && t('plansPage.products.jobPost.vatInclusive') }</span>
                }
              </div>
              <div className='job-products__card-select-jobs'>
                <div className='job-products__card-select-jobs-label'>
                  {
                    jobPostsAmount > 1 && jobPostsAmount <= 5
                      ? <div className='job-products__card-select-jobs-label--green'>
                        <span>{t('plansPage.products.jobPost.savings')}</span>
                        &nbsp;{getPriceWithCurrency(savings, currency)}&nbsp;
                      </div>
                      : t('plansPage.products.jobPost.selectLabel')
                  }
                </div>
                <NumericInput {...this.inputProps()} min={selectJobPostLimits.min} max={selectJobPostLimits.max} />
              </div>
            </div>
            <div className='job-products__card-btn-container'>
              <PrimaryButton onClick={jobPostsAmount > 5 ? openIntercomWithNewMessage : this.onSelectJobs}>
                {jobPostsAmount > 5 ? t('common.talkToSales') : t('common.select')}
              </PrimaryButton>
            </div>
          </div>
        )
    }
  }

  geCustomizedCard = () => {
    const { t } = this.props
    const { tooltipEventType } = this.state

    switch (true) {
      case this.props.isFreePlansPage:
        return (
          <div className='job-products__card job-products__card--free job-products__card--primary'>
            <div className='job-products__card-heading'>
              <div>
                <div className='job-products__card-heading-title'>
                  {t('plansPage.products.customized.title')}
                </div>
                <div className='job-products__card-heading-description'>
                  {t('plansPage.products.customized.description')}
                </div>
              </div>
            </div>
            <div className='job-products__card-content'>
              <table className='job-products__card-table'>
                <tr>
                  <td>
                    <Tooltip trigger={tooltipEventType} content={t('plansPage.products.customized.tooltips.payPerHire')}>
                      <tr className='job-products__card-table-service'>
                        <td><img className='job-products__card-table-service-image' src={payPerHireImage} /></td>
                        <td>
                          <div className='job-products__card-table-service-name'>
                            {t('plansPage.products.customized.services.payPerHire')}
                          </div>
                        </td>
                      </tr>
                    </Tooltip>
                  </td>
                  <td>
                    <Tooltip trigger={tooltipEventType} content={t('plansPage.products.customized.tooltips.cvSearch')}>
                      <tr className='job-products__card-table-service'>
                        <td><img className='job-products__card-table-service-image' src={cvSearchImage} /></td>
                        <td>
                          <div className='job-products__card-table-service-name'>
                            {t('plansPage.products.customized.services.cvSearch')}
                          </div>
                        </td>
                      </tr>
                    </Tooltip>
                  </td>
                </tr>
              </table>
            </div>
            <div className='job-products__card-btn-container'>
              <PrimaryButton onClick={openIntercomWithNewMessage}>{t('common.talkToSales')}</PrimaryButton>
            </div>
          </div>
        )
      default:
        return (
          <div className='job-products__card job-products__card--primary'>
            <div className='job-products__card-heading'>
              <div>
                <div className='job-products__card-heading-title'>
                  {t('plansPage.products.customized.title')}
                </div>
                <div className='job-products__card-heading-description'>
                  {t('plansPage.products.customized.description')}
                </div>
              </div>
            </div>
            <div className='job-products__card-content'>
              <table className='job-products__card-table'>
                <tr>
                  <td>
                    <Tooltip trigger={tooltipEventType} content={t('plansPage.products.customized.tooltips.payPerHire')}>
                      <tr className='job-products__card-table-service'>
                        <td><img className='job-products__card-table-service-image' src={payPerHireImage} /></td>
                        <td>
                          <div className='job-products__card-table-service-name'>
                            {t('plansPage.products.customized.services.payPerHire')}
                          </div>
                        </td>
                      </tr>
                    </Tooltip>
                  </td>
                  <td>
                    <Tooltip trigger={tooltipEventType} content={t('plansPage.products.customized.tooltips.cvSearch')}>
                      <tr className='job-products__card-table-service'>
                        <td><img className='job-products__card-table-service-image' src={cvSearchImage} /></td>
                        <td>
                          <div className='job-products__card-table-service-name'>
                            {t('plansPage.products.customized.services.cvSearch')}
                          </div>
                        </td>
                      </tr>
                    </Tooltip>
                  </td>
                </tr>
                <tr>
                  <td>
                    <Tooltip trigger={tooltipEventType} content={t('plansPage.products.customized.tooltips.jobSlots')}>
                      <tr className='job-products__card-table-service'>
                        <td><img className='job-products__card-table-service-image' src={jobSlotsImage} /></td>
                        <td>
                          <div className='job-products__card-table-service-name'>
                            {t('plansPage.products.customized.services.jobSlots')}
                          </div>
                        </td>
                      </tr>
                    </Tooltip>
                  </td>
                  <td>
                    <Tooltip trigger={tooltipEventType} content={t('plansPage.products.customized.tooltips.subscriptionPlans')}>
                      <tr className='job-products__card-table-service'>
                        <td><img className='job-products__card-table-service-image' src={subscriptionPlansImage} />
                        </td>
                        <td>
                          <div className='job-products__card-table-service-name'>
                            {t('plansPage.products.customized.services.subscriptionPlans')}
                          </div>
                        </td>
                      </tr>
                    </Tooltip>
                  </td>
                </tr>
              </table>
            </div>
            <div className='job-products__card-btn-container'>
              <PrimaryButton onClick={openIntercomWithNewMessage}>{t('common.talkToSales')}</PrimaryButton>
            </div>
          </div>
        )
    }
  }

  render () {
    const { t } = this.props
    return (
      <section>
        <div className='columns'>
          <div className='column col-6 col-xs-12'>
            {this.getJobProductsCard()}
            <div className='job-products__benefits'>
              <div className='columns'>
                <div className='column col-5 col-xs-12 col-sm-12 col-md-12'>
                  <div className='job-products__benefit'>
                    <i className='job-products__benefit-icon' />
                    <span className='job-products__benefit-description'>
                      {t('plansPage.products.jobPost.benefits.expiryPeriod')}
                    </span>
                  </div>
                  <div className='job-products__benefit'>
                    <i className='job-products__benefit-icon' />
                    <span className='job-products__benefit-description'>
                      {t('plansPage.products.jobPost.benefits.companyPage')}
                    </span>
                  </div>
                </div>
                <div className='column col-7 col-xs-12 col-sm-12 col-md-12'>
                  <div className='job-products__benefit'>
                    <i className='job-products__benefit-icon' />
                    <span className='job-products__benefit-description'>
                      {t('plansPage.products.jobPost.benefits.applicantFilters')}
                    </span>
                  </div>
                  <div className='job-products__benefit'>
                    <i className='job-products__benefit-icon' />
                    <span className='job-products__benefit-description'>
                      {t('plansPage.products.jobPost.benefits.support')}
                    </span>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className='column col-6 col-xs-12'>
            {this.geCustomizedCard()}
          </div>
        </div>
      </section>
    )
  }
}

export default withTranslations(JobsProducts)



