import React from "react";
import withTranslation from 'Shared/hoc/withTranslations'


function SummaryWrapper(props) {

    const { t } = props;

    return (
        <div className='order-review__summary'>
            <div className='order-review__summary-heading'>
                <div className='order-review__summary-heading-title'>
                    {t('careerProPage.summary.title')}
                </div>

            </div>

            {props.children}
        </div>
    )
}

export default withTranslation(SummaryWrapper)


