import React from 'react'
import Collapse, { Panel } from 'rc-collapse'
import AccordionPanelHeader from 'Shared/components/AccordionPanel/AccordionPanelHeader'
import './PromoCodeView.scss'
import { imagesCareerPro } from '../imagesCareerPro'
import withTranslation from 'Shared/hoc/withTranslations'
import mixpanel from 'mixpanel-browser'

function PromoCodeView (props) {
  const ICONS = {...imagesCareerPro}
  const { isOpen, promoCodeResponse, onApplyPromoCode, onChangePromoCode, t } = props
  const isValidPromoCode = (promoCodeResponse && promoCodeResponse.promo_code)

  const HEADING = {
    false: t('careerProPage.promocode.input_label'),
    true: t('careerProPage.promocode.input_label_after_applied')
  }

  return (<React.Fragment>
    <div className='order-review__promoCode'>
      <Collapse defaultActiveKey={isOpen} className='promo__collapse-container'>
        <Panel showArrow={false} header={<AccordionPanelHeader> {HEADING[!!isValidPromoCode]} </AccordionPanelHeader>}>
          <div className='promocode_div'>
            <input type='text' name='promoCode'
              placeholder={t('careerProPage.promocode.input_label_after_applied')}
              onChange={(e) => onChangePromoCode(e.target.value)}
              onBlur={(e) => onChangePromoCode(e.target.value, true)}
            />
            <button onClick={() => {onApplyPromoCode(); mixpanel.track("Button Click - Promo Code - Apply")}}>
              {t('careerProPage.promocode.btn_text')}
            </button>
          </div>
        </Panel>
      </Collapse>

      { isValidPromoCode &&
        <div className='promoCodeApplied'> <img src={ICONS.blue_tick} /> {promoCodeResponse.promo_code}</div>
      }

    </div>
  </React.Fragment>)
}

export default withTranslation(PromoCodeView)



