import React from 'react'
import withTranslations from 'Shared/hoc/withTranslations'

const MainHeading = ({ t }) => {
  return (
    <h1 className='order-review__heading'>{t('careerProPage.checkout.heading')}</h1>
  )
}

export default withTranslations(MainHeading)



