import { useState } from 'react'
import { postRequest } from './commonApi'
import { endPoints } from './endPoints'
import { setLocalData, getLocalData } from 'Shared/helpers/session';

const useApiCareerProCheckoutSubmitted = () => {
  const [loading, setLoading] = useState(false)
  const [data, setData] = useState(null)

  const apiCareerProCheckoutSubmitted = async () => {
    const url = endPoints.careerProCheckoutSubmitted
    const data = {
      id: getLocalData('careerProCheckoutId'),
      is_completed: true
    }

    setLoading(true)
    try {
      let res = await postRequest(url, data)
      if (res.success) {
        setData(res.data)
        setLocalData('careerProCheckoutId', res.data.id)
        setLoading(false)
        return res.data
      }
    } catch (err) {
      console.log('Err', err)
      setLoading(false)
    }
  }
  return { loading, data, apiCareerProCheckoutSubmitted }
}

export default useApiCareerProCheckoutSubmitted



