import React, { Component } from 'react'
import withTranslation from 'Shared/hoc/withTranslations'
import Collapse, { Panel } from 'rc-collapse'
import ReactHtmlParser from 'react-html-parser'
import AccordionPanelHeader from 'Shared/components/AccordionPanel/AccordionPanelHeader'
import './FAQ.scss'

class FAQ extends Component {
  componentDidMount () {
    this.props.fetchCurrentLocationIfNeeded()
  }

  renderAccordionPanels = () => {
    return this.props.faqs.map((item, index) => {
      return <Panel key={index} showArrow={false} header={<AccordionPanelHeader>{item.question}</AccordionPanelHeader>}>{ReactHtmlParser(item.answer)}</Panel>
    })
  }

  render () {
    const { t } = this.props
    return (
      this.props.faqs.length > 0
        ? <div className='faq__container'>
          <div className='faq__heading'>
            {t('pricingPage.faq.faqHeading')}
          </div>
          <Collapse className='faq__collapse-container'>
            {this.renderAccordionPanels()}
          </Collapse>
        </div>
        : null
    )
  }
}

export default withTranslation(FAQ)



