import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import { isFreePlansPage } from 'Shared/selectors/geolocation'
import CareerProCheckoutPage from '../../layouts/CareerPro/CareerProCheckoutPage/CareerProCheckoutPage'

const mapStateToProps = state => ({
  userRole: state.user.role,
  isFreePlansPage: isFreePlansPage(state)
})

export default withRouter(connect(mapStateToProps)(CareerProCheckoutPage))



