import React from "react";
import { NavLink } from 'react-router-dom'
import withTranslations from 'Shared/hoc/withTranslations'
import './Navbar.scss'


const Navbar = ({ t }) => {

    return (
            <div className='order-review__steps career_pro_checkout'>
                <NavLink
                    className='order-review__steps-link grey_color_deactive'
                    to={'/career-pro'}>
                    {t('careerProPage.checkout.Navlink.careerpro')}
                </NavLink>

                <NavLink
                    className='order-review__steps-link'
                    activeClassName='order-review__steps-link--active'
                    to={'/career-pro/checkout'}>
                    {t('careerProPage.checkout.Navlink.checkout')}
                </NavLink>

            </div>
    )
}


export default withTranslations(Navbar)




