import React, { useEffect } from 'react'
import { withRouter } from 'react-router-dom'
import StripeElementView from './StripeElementView'
import StripeElementModel from './StripeElementModel'
import withReduxUser from './withReduxUser'
import { getLocalData, setLocalData } from 'Shared/helpers/session'
import useApiCreateCareerProUserInfo from '../services/api/useApiCreateCareerProUserInfo'
import mixpanel from 'mixpanel-browser'

function StripeElementController (props) {
  const { promoCodeUserInput } = props
  const selectedPackage = getLocalData('selected_package') || null
  const selectedPackageId = selectedPackage && selectedPackage.id
  const {
    data, apiStripeIntentModel,
    formStep, setFormStep, stripeUserForm, setStripeUserForm, isValidateUserForm,
    stripeUserFormError
  } = StripeElementModel()
  const { apiCreateCareerProUserInfo } = useApiCreateCareerProUserInfo()

  const handleApiStripeIntent = (pkgId) => {
    apiCreateCareerProUserInfo()
    apiStripeIntentModel(pkgId, promoCodeUserInput, true, stripeUserForm.name, stripeUserForm.email, stripeUserForm.phone );
    mixpanel.track('Career Pro - Form Details - Button Click - Next')
  }

  const fromChangeHandler = (e) => {
    const { value, name, type } = e.target
    setLocalData(`stripe_form_${name}`, value)

    setStripeUserForm((prevState) => ({
      ...prevState,
      [name]: value,
      errors: stripeUserFormError
    }))
  }

  useEffect(() => {
    ((stripeUserForm && stripeUserForm.email) && promoCodeUserInput) && handleApiStripeIntent(selectedPackageId)
  }, [promoCodeUserInput])

  const StripeElementViewWithRedux = withReduxUser(StripeElementView)

  return (<React.Fragment>
    <StripeElementViewWithRedux
      formStep={formStep} setFormStep={setFormStep}
      isValidateUserForm={isValidateUserForm}
      stripeUserForm={stripeUserForm} setStripeUserForm={setStripeUserForm}
      selectedPackageId={selectedPackageId}
      clientSecret={data ? data.client_secret : null}
      handleApiStripeIntent={handleApiStripeIntent}
      fromChangeHandler={fromChangeHandler}
    />
  </React.Fragment>)
}

export default withRouter(StripeElementController)



