import React from 'react'
import Container from 'Shared/components/Container'
import Row from '../Components/Row'
import Col from '../Components/Col'
import MainHeading from '../Components/MainHeading'
import Navbar from '../Components/NavLink/NavLink'
import CheckoutSelectedPlan from './CheckoutSelectedPlan'
import FormWrapper from '../Components/FormWrapper/FormWrapper'
import StripeElementController from '../../../StripeElementController/StripeElementController'
import SummaryWrapper from './SummaryWrapper'
import CareerProCheckoutBenefits from '../Components/CareerProCheckoutBenefits/CareerProCheckoutBenefits'
import CheckoutSummaryController from '../Components/CheckoutSummary/CheckoutSummaryController'
import { getLocalData } from 'Shared/helpers/session'
import './CareerProCheckoutPage.scss'

export default function CareerProCheckoutView (props) {
  const { promoCodeUserInput, promoCodeResponse, onApplyPromoCode, onChangePromoCode,
    PromoCodeView, formStep, setFormStep = () => {} } = props

  const selectedPlan = getLocalData('selected_package')

  return (<Container className='order-review__container'>
    <Row>
      <Col col={5} xs={12} sm={12} md={12}>
        <MainHeading />
        <Navbar />
        <CheckoutSelectedPlan selectedPlan={selectedPlan} />

        <FormWrapper>
          <StripeElementController promoCodeUserInput={promoCodeUserInput}
            formStep={formStep} setFormStep={setFormStep}
          />
        </FormWrapper>
      </Col>
      <Col col={1} xs={12} sm={12} md={12}></Col>
      <Col col={6} xs={12} sm={12} md={12} >
        <div>
          <SummaryWrapper>
            <CheckoutSummaryController selectedPlan={selectedPlan} />
            <PromoCodeView
              promoCodeUserInput={promoCodeUserInput}
              promoCodeResponse={promoCodeResponse}
              onApplyPromoCode={onApplyPromoCode}
              onChangePromoCode={onChangePromoCode}
            />
          </SummaryWrapper>

          <CareerProCheckoutBenefits selectedPlan={selectedPlan} />
        </div>
      </Col>
    </Row>
  </Container>)
}



