import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import JobProducts from 'Shared/layouts/Plans/components/JobsProducts'
import { fetchCurrentLocationIfNeeded } from 'Shared/ducks/geolocation'
import {
  getJobPostBasePrice,
  getPaymentCurrency,
  isFreePlansPage
} from 'Shared/selectors/geolocation'
import { Roles } from 'Shared/constants/User'
import { createNewJob } from 'Shared/ducks/jobOffers'
import { getUserId, userEmail } from 'Shared/selectors/user'

const mapStateToProps = state => ({
  price: getJobPostBasePrice(state),
  currency: getPaymentCurrency(state),
  isFreePlansPage: isFreePlansPage(state),
  isEmployer: state.user.role === Roles.employer.name,
  userEmail: userEmail(state),
  userId: getUserId(state)
})

const mapDispatchToProps = (dispatch, ownProps) => ({
  fetchCurrentLocationIfNeeded: () => dispatch(fetchCurrentLocationIfNeeded()),
  createNewEmptyJob: async () => {
    const { id } = await dispatch(createNewJob({}))
    return ownProps.history.push(`/employer/job-offer/${id}/add`)
  }
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(JobProducts))



