import { useState } from 'react'
import { postRequest } from './commonApi'
import { endPoints } from './endPoints'
import { setLocalData, getLocalData } from 'Shared/helpers/session';

const useApiCreateCareerProUserInfo = () => {
  const [loading, setLoading] = useState(false)
  const [data, setData] = useState(null)

  const apiCreateCareerProUserInfo = async () => {
    const url = endPoints.createCareerProUserInfo
    let userData = {
      name: getLocalData('stripe_form_name'),
      email: getLocalData('stripe_form_email'),
      phone: getLocalData('stripe_form_phone'),
      package_id: getLocalData('selected_package').id
    }
    if (!userData.name || !userData.email || !userData.phone) {
      userData = getLocalData('user_detail')
    }
    setLoading(true)
    try {
      let res = await postRequest(url, userData)
      if (res.success) {
        setData(res.data)
        setLocalData('careerProCheckoutId', res.data.id)
        setLoading(false)
        return res.data
      }
    } catch (err) {
      console.log('Err', err)
      setLoading(false)
    }
  }
  return { loading, data, apiCreateCareerProUserInfo }
}

export default useApiCreateCareerProUserInfo



