import React, { useEffect } from 'react'
import PromoCodeView from './PromoCodeView'
import PromoCodeModel from './PromoCodeModel'

const PromoCodeController = (props) => {
  const { promoCodeResponse, promoCodeUserInput, onChangePromoCode, onApplyPromoCode } = PromoCodeModel();

  const isOpen = '1'

  const onChangePromoCodeController = (arg) => {
    return onChangePromoCode(arg)
  }

  const onApplyPromoCodeController = async (arg) => {
    try {
      const res = onApplyPromoCode(arg)
      return res
    } catch (error) {
      console.log(error)
    }
  }

  useEffect(() => {
    /* When get PromoCode API Response */
  }, [promoCodeResponse])

  const view = () => (<React.Fragment>
    <PromoCodeView
      promoCodeUserInput={promoCodeUserInput} promoCodeResponse={promoCodeResponse} isOpen={isOpen}
      onApplyPromoCode={onApplyPromoCodeController} onChangePromoCode={onChangePromoCodeController}
    />
  </React.Fragment>)

  return { view, promoCodeUserInput }
}

export default PromoCodeController



