import React from 'react'

export default function CheckoutSelectedPlan (props) {
  const { selectedPlan } = props

  let desc = selectedPlan && selectedPlan.description && selectedPlan.description.split('|')
  desc = desc && [desc.join('•')]

  if (!selectedPlan) return <div>Loading...</div>

  return (
    <div>
      <div className='order-review__selected'>
        <h2>{selectedPlan.name}</h2>
        <p>{desc} - <span>{`$${selectedPlan.price}`}</span></p>
      </div>
      <hr className='order-review__hr' />
    </div>
  )
}



