import React from 'react'
import CareerProCheckoutView from './CareerProCheckoutView'
import PromoCodeController from '../Components/PromoCode/PromoCodeController'

const CareerProCheckoutController = () => {
  const { view: PromoCodeView, promoCodeUserInput } = PromoCodeController()

  return (<React.Fragment>
    <CareerProCheckoutView
      PromoCodeView={PromoCodeView} promoCodeUserInput={promoCodeUserInput}
    />
  </React.Fragment>)
}

export default CareerProCheckoutController



