import React from 'react'
import withTranslation from 'Shared/hoc/withTranslations'
import './CareerProPlanSummaryView.scss'

function CareerProPlanSummaryView (props) {
  const { discountedPrice, selectedPlan, t } = props

  if (!selectedPlan) return 'Loading...' // WIP HOC

  let desc = selectedPlan.description.split('|')
  desc = [desc.join('•')]

  return (
    <div className='order-review__summary-content'>
      <div className='order-review__summary-product'>
        <div className='order-review__summary-product-amount'>
          {selectedPlan.name}
        </div>
        <div className='order-review__summary-product-description'>
          {desc}
        </div>
      </div>
      <div className='order-review__summary-price'>
        <div className='order-review__summary-price-label'>{t('careerProPage.summary.total')}</div>
        <div className='order-review__summary-price-amount'><span className={`${!!discountedPrice && 'added_promocode_price'}`}>{`$${selectedPlan.price}`}</span> {!!discountedPrice && `$${discountedPrice}`}</div>
      </div>
    </div>
  )
}

export default withTranslation(CareerProPlanSummaryView)


