export const discountedPrice = ( data, regular_price ) => {
    let res;
    (data.discount_type==="percentage")   
      ?
        res = regular_price * ( (100-data.percentage) / 100 )
      :
        res = regular_price - data.amount
      ;
    
    res = parseFloat(res.toFixed(2))
    
    if(res < 0) res = 0;
  
    return res
}


