import React, { useState, useEffect } from 'react'
import './UserInfo.scss'
import withTranslation from 'Shared/hoc/withTranslations'
import { isValidValue, isEmptyObject } from 'Shared/helpers/validator'
import { setLocalData } from 'Shared/helpers/session'

function UserInfoView (props) {
  const { stripeUserForm, setStripeUserForm, selectedPackageId, formStep, setFormStep, handleApiStripeIntent, t } = props
  const [inputs, setInputs] = useState({})
  const [formError, setFormError] = useState({name:"", email:"", phone:""})
  const [isValidForm, setIsValidForm] = useState(false)

  const ERROR = {
    name: formError.name,
    email: formError.email,
    phone: formError.phone
  }

  const isValidateUserFormHandler = () => {
    const noErrors = Object.values(formError).every((v) => v === true );
    if (noErrors) { setIsValidForm(true); return true }

    setIsValidForm(false)
    return false
  }

  const handleInputChange = (e) => {
    const { value, name, type } = e.target
    setLocalData(`stripe_form_${name}`, value)
    setInputs(prev => ({ ...prev, [name]: value }))
    setFormError((prev) => ({ ...prev, [name]: !!isValidValue(value, type, true) }))
    isValidateUserFormHandler()
  }

  const handleSubmit = () => {
    if (!isValidForm) return
    setFormStep(2)
    setStripeUserForm({ name: inputs.name, email: inputs.email, phone: inputs.phone })
    handleApiStripeIntent(selectedPackageId)
  }

  useEffect(() => {
    (stripeUserForm.name && stripeUserForm.email && stripeUserForm.phone) && setIsValidForm(true)
  }, [])

  return (<React.Fragment>
    <form autoComplete='off'>
      <div className='order-review__user_form'>
        <h3>{t('careerProPage.checkout.form.title', 'Enter your details')}</h3>
        <div className='form-group' data-valid={ERROR['name']} >
          <label>{t('careerProPage.checkout.form.full_name_label', 'Full Name')}</label>
          <input name='name' type='text' placeholder={t('careerProPage.checkout.form.full_name_placeholder', 'Your Name')} defaultValue={stripeUserForm.name}
            onKeyUp={(e) => handleInputChange(e)}
            onChange={(e) => handleInputChange(e)}
            required
            autocomplete='none'
          />
        </div>

        <div className='form-group' data-valid={ERROR['email']}>
          <label>{t('careerProPage.checkout.form.email_label', 'Email')}</label>
          <input name='email' type='email' placeholder={t('careerProPage.checkout.form.email_placeholder', 'Email Address')}
            defaultValue={stripeUserForm.email}
            onKeyUp={(e) => handleInputChange(e)}
            onChange={(e) => handleInputChange(e)}
            required
            autocomplete='none'
          />
        </div>

        <div className="form-group" data-valid={ERROR['phone']}>
          <label>{t('careerProPage.checkout.form.phone_label','Phone Number')}</label>
          <input name='phone' type='tel' placeholder={t('careerProPage.checkout.form.phone_placeholder','Phone Number')}
            defaultValue={stripeUserForm.phone}
            onKeyUp={(e) => handleInputChange(e)}
            onChange={(e) => handleInputChange(e)}
            required
            autocomplete='none'
            onKeyPress={(event) => {
              if (!/[0-9]/.test(event.key)) {
                event.preventDefault()
              }
            }}
          />
        </div>
      </div>

      <button className={`${(formStep === 2) && 'tabs-active'} ${!isValidForm && 'disabled_btn'} tabs btnTheme`}
        onClick={() => {
          handleSubmit()
        }}
        disabled={!isValidForm}
      >
        { isValidForm ? 'Next' : 'Next' }
      </button>
    </form>
  </React.Fragment>)
}

export default withTranslation(UserInfoView)



