import { useEffect, useState } from 'react'
import useApiStripeIntent from '../services/api/useApiStripeIntent'
import { debounce } from 'Shared/helpers/debounce'

export default function StripeElementModel () {
  const userInfoDefault = { name: '', email: '', phone: '' }
  const { data, apiRequest: apiStripeIntent } = useApiStripeIntent()
  const [stripeUserForm, setStripeUserForm] = useState({userInfoDefault})
  const [stripeUserFormError, setStripeUserFormError] = useState(false)

  const [formStep, setFormStep] = useState(formStep || 1)
  const [isValidateUserForm, setIsValidateUserForm] = useState(false)

  const apiStripeIntentModel = (packageId, promoCode, is_new = true, name, email, phone) => {
    apiStripeIntent(packageId, promoCode, is_new=true, name, email, phone) // This order should be same with useApiStripeIntent
  }

  const isValidate = (form) => {
    let validated = (form) && (!!form.name === true && !!form.email === true && !!form.phone === true)
    return validated
  }

  useEffect(() => {
    debounce(setIsValidateUserForm(isValidate(stripeUserForm)), 4000)
  }, [stripeUserForm])

  return {
    formStep,
    setFormStep,
    data,
    apiStripeIntentModel,
    stripeUserForm,
    setStripeUserForm,
    isValidateUserForm,
    stripeUserFormError,
    setStripeUserFormError
  }
}



