import React from 'react'
import withTranslations from 'Shared/hoc/withTranslations'
import { PrimaryButtonOutline } from 'Shared/components/Button'
import huaweiLogo from 'Shared/assets/images/logos/huawei.svg'
import samsungLogo from 'Shared/assets/images/logos/samsung-logo-about-us.svg'
import sephoraLogo from 'Shared/assets/images/logos/sephora-logo-about-us.svg'
import omnicomLogo from 'Shared/assets/images/logos/omnicom-logo-about-us.svg'
import boseLogo from 'Shared/assets/images/logos/bose-logo-about-us.svg'

const Companies = ({ t }) => (
  <section className='plans__companies-container'>
    <div className='columns'>
      <div className='column col-9 col-xs-12'>
        <div className='plans__companies-title'>{t('plansPage.companies.title')}</div>
        <div className='plans__companies-description'>{t('plansPage.companies.description')}</div>
      </div>
      <div className='column col-3 col-xs-12'>
        <div className='plans__companies-btn-container'>
          <a href='https://employers.oliv.com/customers/' target='_blank'>
            <PrimaryButtonOutline>{t('plansPage.companies.cta')}</PrimaryButtonOutline>
          </a>
        </div>
      </div>
    </div>
    <div className='plans__companies-logos'>
      <img className='plans__companies-logo' src={samsungLogo} />
      <img className='plans__companies-logo' src={boseLogo} />
      <img className='plans__companies-logo plans__companies-logo--height' src={huaweiLogo} />
      <img className='plans__companies-logo' src={sephoraLogo} />
      <img className='plans__companies-logo' src={omnicomLogo} />
    </div>
  </section>
)

export default withTranslations(Companies)



