import { useState } from "react";
import { postRequest } from "./commonApi";
import { endPoints } from "./endPoints";
import { getLocalData } from "Shared/helpers/session";


const useApiStripeIntent = () => {
  
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState(null);
  const [status, setStatus] = useState(null);

  const url = endPoints.getPaymentIntent;
  
  const apiRequest = async ( packageId, promo_code=null, is_new=true, name, email, phone, redirectTo) => {
 
    const data = {
      package_id: packageId,
      is_new: is_new,
      domain: window.location.host,
      promo_code: promo_code,
      name: getLocalData('stripe_form_name') || name,
      email: getLocalData('stripe_form_email') ||email,
      phone: getLocalData('stripe_form_phone') || phone
  };

    setLoading(true);

    try{
  
      let res = await postRequest(url, data);
  
      if(loading) {
        setLoading(false);
      }

      if(res.success) {
        setData(res.data)
       
        if(notify) {
          console.log(res.message);
          return
        }
      }

      if(!res.success){
        console.log(res.message);
        return;
      }
  
      return res.data;

    } catch(err) {
        console.log("Err", err);
    }

  };

  return { loading, data, status, apiRequest };

};

export default useApiStripeIntent;


