import React, { useEffect } from 'react'
import { withRouter } from 'react-router-dom'
import CheckoutSummaryView from './CheckoutSummaryView'
import { getLocalData, removeLocalData } from 'Shared/helpers/session'
import { discountedPrice } from 'Shared/helpers/discountedPrice'

const CheckoutSummaryController = (props) => {
  const hasPromoCode = getLocalData('promocode_result')
  const discounted = hasPromoCode && discountedPrice(hasPromoCode, props.selectedPlan.price)

  if (!props.selectedPlan) props.history.push('/')

  useEffect(() => {
    removeLocalData('promocode')
    removeLocalData('promocode_result')
  }, [hasPromoCode])

  return (<React.Fragment>
    <CheckoutSummaryView discountedPrice={discounted} {...props} />
  </React.Fragment>)
}

export default withRouter(CheckoutSummaryController)



