import React, { useState } from 'react'
import {useStripe, useElements, PaymentElement} from '@stripe/react-stripe-js'
import { getLocalData } from 'Shared/helpers/session'
import useApiCareerProCheckoutSubmitted from '../services/api/useApiCareerProCheckoutSubmitted'
import mixpanel from 'mixpanel-browser'

const StripeCheckoutForm = (props) => {
  const { stripeUserForm } = props
  const hasUserDetail = getLocalData('user_detail')
  const { apiCareerProCheckoutSubmitted } = useApiCareerProCheckoutSubmitted()

  const stripe = useStripe()
  const elements = useElements()

  const [isLoading, setIsLoading] = useState(false)
  const [allowSelect, setAllowSelect] = useState(true)
  const [error, setError] = useState(null)

  const handleSubmit = async (event) => {
    event.preventDefault()
    setAllowSelect(false)

    if (!stripe || !elements) {
      console.log('Stripe not loaded')
      setAllowSelect(true)
      return
    }

    apiCareerProCheckoutSubmitted()
    let result
    setIsLoading(true)
    result = await stripe.confirmSetup({
      elements,
      confirmParams: {
        return_url: `${process.env.REACT_APP_ROOT_URL}/career-pro`,
        payment_method_data: {
          billing_details: {
            email: stripeUserForm.email,
            name: stripeUserForm.name,
            phone: stripeUserForm.mobile
          }
        }
      }
    })

    if (result) {
      setIsLoading(false)
    }

    if (result.error) {
      console.log(result.error.message)
      setError(result.error.message)
    }

    setAllowSelect(true)
  }

  return (
    <form>
      <PaymentElement />
      <button className='btnTheme btn_stripe' type='button' onClick={(e) => {
                    handleSubmit(e);
                    // mixpanel.track("Career Pro - Package Payment - Button Click - Submit")
                }}>Submit</button>
      <small className='small'>{ error && error}</small>
    </form>
  )
}

export default StripeCheckoutForm



